import React from "react";
import Map from "../components/Map/Map";
import { NavLink } from "react-router-dom";

const MapPage = () => {
  const paragraphClass = "md:text-xl text-l my-4";

  return (
    <div className="text-justify">
      <Map />
      <p className="text-4xl my-8 font-bold">Mapa Szablonw</p>
      <p className={paragraphClass}>
        Spogldajc na map wszystkich murali, atwo jest zaplanowa sobie tras
        spacerow lub rowerow, w ktrej moemy zobaczy wszystkie szablony.
      </p>
      <p className={paragraphClass}>
        Lokalizacja wielu z nich jest nieprzypadkowa.{" "}
        <NavLink to={`/biogram/Hanys Rybark`}>
          <span className=" underline"> Hanys Rybark</span>
        </NavLink>{" "}
        z zadum spoglda na swj dom rodzinny na ulicy 3 maja,
        <NavLink to={`/biogram/Melchior Grossek`}>
          <span className=" underline"> Melchior Grossek</span>
        </NavLink>{" "}
        rwnie znajduje si niedaleko swojego domu na Wrocawskiej, a jego brat
        <NavLink to={`/biogram/Stanisaw Grossek`}>
          <span className=" underline"> Stanisaw</span>
        </NavLink>{" "}
        patrzy w kierunku ulicy Braci Grosskw
      </p>
      <p className={paragraphClass}>
        Spacery polecam rozpocz od "starego" Bralina i Hanysa Rybarka, czyli
        ulicy 3-maja, pniej zobaczy obrazy Melchiora na budynku starej
        braliskiej gospody. Nastpnie ulic Krt (Kutzner) i Spokojn przej
        na ulic Ogrodow (ks. Wojciechowski), skrci na Now (Antoni Gabriel),
        odbi jeszcze na Kalinow (Przywara), eby potem przej Lipow
        (Stanisaw Grossek, Jan Nowak, Ignacy Majchrzak) a do Kocielnej na
        kocu ktrej czeka Tomasz Gabriel. Koczc spacer, jestemy niedaleko
        Pomnika Ofiar, Represji i Walki o Wolno, oryginalne wzniesiony po I
        w., braliskiego cmentarza, ale te placu zabaw obok starego kocioa
        ewangelickiego.
      </p>
    </div>
  );
};

export default MapPage;
