import React from "react";

const AboutPage = () => {
  const paragraphClasses = `lg:text-xl md:text-xl text-l`;
  const largeTitle = "text-4xl font-bold my-4 ";
  const mediumTitle = "text-2xl font-bold my-4";
  return (
    <div className="max-w-4xl mx-auto text-justify">
      <iframe
        width="100%"
        // height="600"
        src="https://www.youtube.com/embed/IdWoyn_0f7M"
        title="Szablony lokalne"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowFullScreen
        className="mb-8 md:h-[600px] h-[300px]"
      ></iframe>
      <p className={largeTitle}>Co to?</p>
      <p className={paragraphClasses}>
        Szablony LOKALne projekt, ktry zakada oddanie hodu i szacunku
        wybitnym jednostkom, w historii naszej lokalnej spoecznoci, poprzez
        seri szablonw (technika graffiti).
      </p>
      <p className={largeTitle}>Dlaczego?</p>
      <div>
        <p className={mediumTitle}>Pami</p>
        <p className={paragraphClasses}>
          Chciaem odda hod ludziom o wyjtkowym poczuciu moralnoci,
          inteligencji, wraliwoci, peni troski o ycie spoeczne mieszkacw,
          ktrzy nie zwaajc na swoje ycie dziaali w sprawach niepodlegoci
          Polski. Ludziom, ktrzy powinni by wzorem (szablonem!) dla nas.
          Tosamo, zbir postaw i wartoci nie bierze si z powietrza, ona
          tutaj istniaa ju wczeniej, tylko naley o niej przypomina, eby j
          zachowa. Historie namalowanych osb, s rne, wi si z wieloma
          wyzwaniami, z ktrymi musieli si w swoich yciach mierzy, I w., II
          w., obozy koncentracyjne, Katy, walka o niepodlego. Midzy tymi
          wydarzeniami, potrafili by inspiracj dla modych ludzi, edukowa
          ich, prowadzi ycie naukowe czy artystyczne.
        </p>
      </div>
      <div>
        <p className={mediumTitle}>Edukacja</p>
        <p className={paragraphClasses}>
          Powszechna forma pozwala na atwiejsze dotarcie do wszystkich
          mieszkacw bez podziau na wiek (edukacja nie tylko w wieku
          szkolnym). Chciaem, eby szablony wpisay si w krajobraz naszej
          miejscowoci, eby ludzie je widzieli drodze do domu, do pracy,
          gdziekolwiek w Bralinie mieszkaj. Osoby, upamitnione muralami, byy
          postaciami, ktre powiecay swoj energi i ycie innym, dla ktrych
          dobro spoecznoci, byo czsto waniejsze ni dobro wasne. O yciu
          kadego z nich, mona przeczyta z tablic informacyjnych, ktre
          widniej przy malunkach.
        </p>
      </div>
      <div>
        <p className={mediumTitle}>Estetyka</p>
        <p className={paragraphClasses}>
          Mam nadziej, e jakiej czci naszej spoecznoci te murale, po
          prostu wydadz si adne, estetyczne, a nowe wersje tych do tej pory,
          mniej lub bardziej anonimowych pustych cian, oka si miymi
          akcentami w krajobrazie Bralina.
        </p>
      </div>
      <p className={largeTitle}>Kiedy?</p>
      <p className={paragraphClasses}>
        Pierwszy mural powsta jesieni 2022, najpniejszy (ale nie ostatni) w
        kwietniu 2023 roku.
      </p>
      <p className={largeTitle}>Kto?</p>
      <p className={paragraphClasses}>
        Dzikuj wszystkim sponsorom, ktrzy pomogli mi sfinansowa farby oraz
        tablice informacyjne. Dzikuj pastwu Magorzacie i Cezaremu
        Grzekowiak, pastwu Kindze i Damianowi Kozicy oraz mojemu bratu
        Kacprowi Rozdolskiemu. Dzikuj panu Jackowi Kuropce za pomoc w
        napisaniu i zredagowaniu tekstw biograficznych, oraz za dyskusje
        historyczne. Dzikuj wszystkim, ktrzy zgodzili si eby na murach,
        ktre do nich nale znalazy szablony. Dziki Wam Bralin jest odrobin
        ciekawiej wygldajc miejscowoci. Dzikuj swojej onie Monice, za
        wsparcie ktre mi daa przez cay ten dugi czas realizacji projektu.
      </p>
    </div>
  );
};

export default AboutPage;
